<template>
    <div class="text_list_wait">
        <div v-for="(item,index) in notOverList" :key="index" class="activity-list-item" @click="startText(item)">
            <div class="activity-list-div">
                <div class="activity-list-right">
                    <div  class="show-img">
                        <img src="../../assets/image/企业管理考试.png" alt="" v-if='item.specialCode == GLOBAL.SPECIALCODE.qyglCode'>
                        <img src="../../assets/image/业务技能考试.png" alt="" v-if='item.specialCode == GLOBAL.SPECIALCODE.ywjnCode'>
                        <img src="../../assets/image/质量管理考试.png" alt="" v-if='item.specialCode == GLOBAL.SPECIALCODE.zlgkCode'>
                        <img src="../../assets/image/安全生产考试.png" alt="" v-if='item.specialCode == GLOBAL.SPECIALCODE.aqscCode'>
                        <img src="../../assets/image/其他考试.png" alt="" v-if='item.specialCode == GLOBAL.SPECIALCODE.qtCode'>
                        <img src="../../assets/image/泛在物联网考试.png" alt="" v-if='item.specialCode == GLOBAL.SPECIALCODE.fzwlwCode'>
                    </div>
                    <div class='show_center'>
                        <div class="activity-list-info">
                            <div class="activity-list-actName">
                                <div>{{ item.name }}</div>
                            </div>
                        </div>
                        <div class="activity-list-word">
                            <div class='bukao' v-if="item.bkCount != 0">补考</div>
                            <div class="activity-list-time" v-if="item.endTime">
                                <div>{{item.startTime}} 至 {{item.endTime}}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="show-btn">
                    <img src="../../assets/image/进入考试.png" alt="">
                    <!-- <li v-if="item.bkCount == 0" class="show-btn-mes start">初考</li>
                    <li v-if="item.bkCount != 0" class="show-btn-mes start">补考</li> -->
                </div>
            </div>
        </div>
        <div class='empty'>
            <img src="../../assets/image/空空如也.png" alt="">
            <div class='emptyWord'>空空如也~</div>
        </div>
        <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>

<script>
import Loading from './../components/loading'
import API from '@/api/api'
import $ from 'jquery'

export default {
  name: 'text_list_wait',
  components: {
    Loading
  },
  data () {
    return {
      notOverList: [], // 待考集合
      verDialogVisible: true // 加载页
    }
  },

  mounted () {
    this.$parent.isChange = 0
    $('.empty').hide()
    // StatusBar.styleLightContent();
    $('#not-go').addClass('hit-add-Class')
    // this.returnButton()
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      const that = this
      API.post('getTotalExamPlanTotalInfo', {
        type: 0
      }, 'json', (data, err) => {
        if (!data.success) {
          alert(data.msg)
        } else if (data.success) {
          // console.log(data)
          if (data.data.length === 0) {
            $('.empty').show()
          } else {
            $('.empty').hide()
            that.forBackData(data.data)
          }
          this.verDialogVisible = false
        }
      })
    },
    // 循环返回的数据
    forBackData (list) {
      for (var i = 0; i < list.length; i++) {
        var nameMes = list[i].name// 考试名称
        var startTimeMes = list[i].examEntities[0].selfExamEntities[0].startTime
        var endTimeMes = list[i].examEntities[0].selfExamEntities[0].endTime
        // var startTimeMes = list[i].startTime;//考试计划开始时间
        // var endTimeMes = list[i].endTime;//考试计划开始时间
        // 解析考试信息
        for (var k = 0; k < list[i].examEntities.length; k++) {
          for (var l = 0; l < list[i].examEntities[k].selfExamEntities.length; l++) {
            // 判断个人成绩中的status为0300时说明是未考
            if (list[i].examEntities[k].selfExamEntities[l].status === '0300') {
              var noObj = {}
              noObj.name = nameMes
              noObj.score = ''
              noObj.startTime = this.GLOBAL.changeYear(startTimeMes)
              noObj.textStartTime = startTimeMes
              noObj.id = list[i].examEntities[k].id// 考试id
              noObj.selfExamId = list[i].examEntities[k].selfExamEntities[l].id
              noObj.pass = ''// 0:不及格;1:及格
              noObj.status = list[i].examEntities[k].selfExamEntities[l].status
              noObj.bkCount = list[i].examEntities[k].bkCount // 是否是补考
              noObj.endTime = this.GLOBAL.changeYear(endTimeMes)
              noObj.specialCode = list[i].specialCode
              this.notOverList.push(noObj)
            }
          }
        }
      }
    },
    // 进入考试注意事项页面
    startText (item) {
      this.$router.push({
        name: 'text_list_start',
        params: { id: item.id, selfExamId: item.selfExamId, startTime: item.textStartTime }
      })
    }
  }

}
</script>
<style scoped>
    .text_list_wait{
       width: 100%;
       height: calc(100vh - 0.94rem);
       margin-top: 0.94rem;
       background: rgba(245,246,248,1);
       /* max-height: calc(100vh - 1.92rem); */
       overflow: scroll;
       -webkit-overflow-scrolling: touch;
       /* position: relative; */
    }
    .activity-list-item {
        width: 100%;
        height: 1.8rem;
        padding: 0 0.35rem;
        margin-top:0.1rem;
        background: #fff;
    }
    .activity-list-right {
        width: 100%;
        display: flex;
        height: 100%;
        align-items: center
    }
    .show-img{
        float: left;
        width: 1.11rem;
        height: 1.11rem;
        /* background-size: cover;
        background-image: url('../../assets/image/组13.png'); */
        display: flex;
        justify-content: space-between;
    }
    .show-img img{
        width: 1.11rem;
        height: 1.11rem;
        display: block;
    }
    .show_center{
        margin-left:0.3rem;
        height:1.11rem;
        padding-bottom:0.1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .activity-list-actName div{
        width:4rem;
        font-size: 0.36rem;
        color: #4E4E4E;
        overflow: hidden;
        padding-top:0.1rem;
        text-overflow:ellipsis;
        white-space: nowrap;
        height:0.6rem;
    }
    .activity-list-word{
        display: flex;
    }
    .activity-list-word .bukao{
        width:0.84rem;
        height:0.32rem;
        padding:0 0.08rem;
        /* line-height: 0.35rem; */
        /* height: 0.33rem; */
        background: #FFF1EB;
        color:#FFA06B;
        font-size:0.24rem;
        /* padding-top:0.06rem; */
        line-height: 0.36rem;
        /* display: flex;
        align-items: center;
        justify-content: center; */
        text-align: center;
        margin-right:0.09rem;
        border-radius: 0.06rem;
    }
    .activity-list-time {
        width: 100%;
        height: 0.33rem;
        line-height: 0.33rem;
        /* line-height:0.4rem; */
        font-size: 0.26rem;
        font-family:PingFang-SC-Medium;
        /* font-weight:500; */
        color:#9FA7AF;
        display: flex;
    }
    .show-btn{
        display: flex;
        align-items:center;
        height: 100%;
        padding-right:0.1rem;
    }
    .show-btn img{
        width:0.75rem;
        height:0.75rem;
        display: block;
    }
    .activity-list-div {
        width: 100%;
        height: 1.8rem;
        display: flex;
        justify-content: space-between;
        background:rgba(255,255,255,1);
        border-radius:0.1rem;
        border: 0.01rem solid #FFFFFF;
    }
    .empty{
        /* margin-top:2.75rem; */
        display: flex;
        flex-direction: column;
        width:100%;
        align-items: center;
        position: absolute;
        top:0.94rem;
        padding-top:1.81rem;
        left:0;
        z-index: 3;
        height:calc(100vh - 0.94rem);
        background: #fff;
    }
    .empty img{
        width:2.91rem;
        height:2.63rem;
    }
    .empty .emptyWord{
        margin-top:0.4rem;
        color:#9FA7AF;
        font-size:0.32rem;
    }
</style>
<style>
 #app{
        height: 100%;
        padding: 0 !important;
    }
    .top_all{
       background: rgba(242,242,242,1);
    }
</style>



